"use client";

import Footer from "@/components/footer";
import Sidebar from "@/components/sidebar";
import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";

import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

type Parent = {
  id: string;
  fullname: string;
  contact_number: string;
  address: {
    purok: string;
    baranggay: string;
    municipality: string;
    province: string;
  };
  auth: {
    email: string;
  };
  lastLogin: string | null;
};

const ParentManagement = () => {
  const queryClient = useQueryClient();
  const { parentsList } = useProtectedRoutesApi();
  const [editableData, setEditableData] = useState<{ [key: string]: Parent }>(
    {}
  );

  const { data, isLoading, isError, error } = useQuery({
    queryKey: ["parents"],
    queryFn: parentsList,
  });

  // const mutation = useMutation({
  //   mutationFn: (updatedParent: Parent) => updateParent(updatedParent),
  //   onSuccess: () => {
  //     queryClient.invalidateQueries({ queryKey: ["parents"] });
  //   },
  // });

  const handleEdit = (parent: Parent) => {
    setEditableData((prev) => ({
      ...prev,
      [parent.id]: { ...parent },
    }));
  };

  const handleSave = async (parentId: string) => {
    const updatedParent = editableData[parentId];
    if (updatedParent) {
      // await mutation.mutateAsync(updatedParent);
      setEditableData((prev) => {
        const newState = { ...prev };
        delete newState[parentId];
        return newState;
      });
    }
  };

  const handleChange = (parentId: string, field: string, value: string) => {
    setEditableData((prev) => ({
      ...prev,
      [parentId]: {
        ...prev[parentId],
        [field]: value,
      },
    }));
  };

  const handleAddressChange = (
    parentId: string,
    field: string,
    value: string
  ) => {
    setEditableData((prev) => ({
      ...prev,
      [parentId]: {
        ...prev[parentId],
        address: {
          ...prev[parentId]?.address,
          [field]: value,
        },
      },
    }));
  };

  if (isLoading) return <div>Loading...</div>;
  if (isError) return <div>Error: {error.message}</div>;

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      <Sidebar />
      <main className="p-8 sm:p-20 font-[family-name:var(--font-geist-sans)] flex flex-col gap-8">
        <div>
          <Table>
            <TableCaption>List of Registered Parents</TableCaption>
            <TableHeader>
              <TableRow>
                <TableHead>Full Name</TableHead>
                <TableHead>Contact Number</TableHead>
                <TableHead>Email</TableHead>
                <TableHead>Address</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {data?.data?.map((parent: Parent) => {
                const isEditing = !!editableData[parent.id];
                const currentParent = editableData[parent.id] || parent;

                return (
                  <TableRow key={parent.id}>
                    <TableCell>
                      <Input
                        value={currentParent.fullname}
                        onChange={(e) =>
                          handleChange(parent.id, "fullname", e.target.value)
                        }
                        disabled={!isEditing}
                      />
                    </TableCell>
                    <TableCell>
                      <Input
                        value={currentParent.contact_number}
                        onChange={(e) =>
                          handleChange(
                            parent.id,
                            "contact_number",
                            e.target.value
                          )
                        }
                        disabled={!isEditing}
                      />
                    </TableCell>
                    <TableCell>
                      <Input
                        value={currentParent.auth.email}
                        onChange={(e) =>
                          handleChange(parent.id, "auth", {
                            ...currentParent.auth,
                            email: e.target.value,
                          })
                        }
                        disabled={!isEditing}
                      />
                    </TableCell>
                    <TableCell>
                      <div className="flex flex-col gap-2">
                        <Input
                          value={currentParent.address.purok}
                          onChange={(e) =>
                            handleAddressChange(
                              parent.id,
                              "purok",
                              e.target.value
                            )
                          }
                          disabled={!isEditing}
                          placeholder="Purok"
                        />
                        <Input
                          value={currentParent.address.baranggay}
                          onChange={(e) =>
                            handleAddressChange(
                              parent.id,
                              "baranggay",
                              e.target.value
                            )
                          }
                          disabled={!isEditing}
                          placeholder="Baranggay"
                        />
                        {/* Add more address fields as needed */}
                      </div>
                    </TableCell>
                    <TableCell>
                      {isEditing ? (
                        <Button
                          onClick={() => handleSave(parent.id)}
                          // disabled={mutation.isPending}
                        >
                          {/* {mutation.isPending ? "Saving..." : "Save"} */}
                        </Button>
                      ) : (
                        <Button onClick={() => handleEdit(parent)}>Edit</Button>
                      )}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </div>
      </main>
      <Footer />
    </div>
  );
};

export default ParentManagement;
